@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;/* w3c */
  background-color: #FFF6ED;
}
.cardwidth{
	transition: all 0.5s;
}
.cardwidth:hover{
	transform: scale(1.03);
}
.rearrange{
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;
	justify-content: center;
}
.rlink:hover, .rrlink:active{
	background-color: #E64398;
}
.rlink:link, .rlink:visited{
  background-color: #F172A1;
  color: white;
  padding: 14px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 2px;
}
.text{
	color: black;
	font-size: 20px;
}
.a{
	list-style-type: circle;
	text-align: left;
}
.restaurant{
	font-family: 'Oswald', sans-serif;
	font-size: 25px;
}

@font-face {
font-style: normal;
font-weight: normal;
}

.loading {
  font-weight: 200;
  color: #0ccac4;
}

.banner{
	background-color: #A31620;
	padding: 25px;
}
.title{
	text-align: left;
	padding-left: 6px;
	margin: 0px;
	color: white;
}
